import React, {Component} from 'react';
import {
    Badge,
    Box,
    Button,
    Container,
    CssBaseline,
    Grow,
    Tooltip, Typography
} from '@material-ui/core';
import {random, sample} from "lodash";

import realData from "./real.json";
import fakeData from "./fake.json";
import {ArrowForward, Check, Clear} from "@material-ui/icons";

const colorOK = "#1B5E20", colorNotOK = "#b71c1c";

function searchInNewTab(text) {
    const url = `http://www.google.com/search?q="${text}"+site:wiadomosci.tvp.pl`;
    window.open(url, "_blank");
}

function Option(props) {

    const color = props.over ? (props.showCorrect ? (props.isCorrect ? colorOK : colorNotOK) : "#9E9E9E") : "#212121";

    return <div style={{display: "flex"}}>
        <div style={{
            width: props.over ? 32 : 0,
            transition: "width 150ms ease-in-out"
        }}>
            {props.over && <Grow in={true} style={{marginTop: ".275em"}}>
                {props.isCorrect ? <Check style={{color: color}}/> :
                    <Clear style={{color: color}}/>}
            </Grow>}
        </div>
        <Button size={"large"}
                style={{flexGrow: 1, color: color}}
                onClick={() => {
                    props.over ? searchInNewTab(props.text) : props.onClick()
                }}>{props.text}</Button>
    </div>
}


class Level extends Component {
    constructor(props) {
        super(props);
        const n = 4;
        const correct = random(0, n - 1);
        const options = [];
        for (let i = 0; i < n; ++i) options.push(sample(i === correct ? realData : fakeData));
        this.state = {
            correct: correct, options: options, over: false, clicked: null
        }
    }

    render() {
        return <Grow in={true}>
            <Box>
                <Typography>
                    Który pasek pojawił się w <em>Wiadomościach?</em>
                </Typography>
                <Box mt={2}>
                    {this.state.options.map((option, i) =>
                        <Box key={i}>
                            <Option text={option}
                                    isCorrect={i === this.state.correct}
                                    over={this.state.over}
                                    onClick={() => {
                                        this.setState({over: true, clicked: i});
                                    }}
                                    showCorrect={i === this.state.correct || i === this.state.clicked}/></Box>)}
                </Box>
                <Box mt={2}>
                    <Grow in={this.state.over} style={{float: "right"}}>
                        <Button variant={"contained"} size={"large"}
                                color={"primary"}
                                m={1} endIcon={<ArrowForward/>} onClick={() => {
                            this.props.onNext(this.state.clicked === this.state.correct)
                        }}>
                            Dalej
                        </Button>
                    </Grow>
                </Box>
            </Box>
        </Grow>;
    }
}


export default class App extends Component {
    constructor(props) {
        super(props);

        this.state = {
            level: 0,
            correct: 0,
        }
    }

    render() {
        const correct = this.state.correct,
            incorrect = this.state.level - this.state.correct;
        return <Container style={{maxWidth: 700}}>
            <CssBaseline/>
            <Box mt={2} mb={2} display={"flex"} justifyContent={"flex-end"}>
                <Box>
                    <Tooltip title={`Poprawne odpowiedzi: ${correct}`}>
                        <Badge badgeContent={this.state.correct} showZero={true}
                               overlap={"circle"} color={"primary"}>
                            <Check fontSize={"large"} color={"primary"}/>
                        </Badge>
                    </Tooltip>
                </Box>
                <Box>
                    <Tooltip title={`Niepoprawne odpowiedzi: ${incorrect}`}>
                        <Badge
                            badgeContent={this.state.level - this.state.correct}
                            showZero={true} overlap={"circle"}
                            color={"primary"}>
                            <Clear fontSize={"large"} color={"primary"}/>
                        </Badge>
                    </Tooltip>
                </Box>
            </Box>
            <Level key={this.state.level} over={this.state.over}
                   onNext={(ok) => this.setState({
                       level: this.state.level + 1,
                       correct: this.state.correct + (ok ? 1 : 0)
                   })}/>
        </Container>
    }
}
